<template>
    <div>
        <ul>
          <li class="list-item clearfix" v-for="(item,index) in movieList" :key="index" @click="goDetail(item.id)">
            <div class="list-left">
              <img :src="item.cover.url" alt="">
            </div>
            <div class="list-right">
              <h4>{{item.title}}</h4>
              <span v-for="(val,index) in item.actors" :key="index">{{val}}/</span>
            </div>
          </li>
        </ul>
        <div class="mask" v-show="!isShow">
          <div class="content">
            <img src="../../assets/img/loading.gif" alt="">
          </div>
        </div>
    </div>
</template>
<script>
export default {
  data(){
    return{
      movieList:[],
      listStart:0,
      isShow:true,
      // id:[]
    }
  },
mounted() {
    window.onscroll = () =>{
      let clientHeight = document.documentElement.clientHeight;//可视区域高度
      let scrollTop = document.documentElement.scrollTop;//当前滚动高度
      let scrollHeigth = document.documentElement.scrollHeight;//滚动条可滚动高度
      if(clientHeight+scrollTop>=scrollHeigth-10 && this.isShow && this.movieList.length !=25){
        this.listStart+=10;
        this.getList()
      }
    }
  },
  created(){
    let obj = {
      title:"电影",
      className:"movie"
    }
    this.$emit("changeNav",obj)
      this.getList()
  },
  methods: {
    getList(){
      let proxy='https://bird.ioliu.cn/v2?url=';
      let url=`https://m.douban.com/rexxar/api/v2/subject_collection/movie_showing/items?start=${this.listStart}&count=10`;
      this.isShow = false;
      axios.get(proxy+url).then((res) => {
        console.log(res);
        console.log(res.data.subject_collection_items);
        this.movieList=this.movieList.concat(res.data.subject_collection_items);
        this.isShow=true;
      }).catch((res) => {
        console.log('失败');
      })
    },
    goDetail(id){
      // for(let item in this.movieList){
      //     this.id=this.id.concat(this.movieList[item].id);
      // }
      // console.log(id);
      // 编程式导航写name
      this.$router.push(`/movieDetail/${id}`);
    }

  }
}
</script>
<style scoped>
ul{
  padding: 10px;
}
  .list-item{
    border-bottom: 1px solid rgb(184, 152, 152);
    width: 100%;
    padding: 20px 0;
  }
  .list-item .list-left{
    float: left;
    width: 40%;
    text-align: center;
  }
  .list-item .list-right{
    float: right;
    width: 50%;
    padding:  0 5%;
  }
  .list-item .list-left img{
    width: 100%;
  }
  .mask{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.3);
    display: flex;
    justify-content: center;
    align-content: center;
  }
  .mask .content{
    height: 200px;
    width: 200px;
    background-color: rgba(255,255,255,.6);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    border-radius: 10px;
  }
  .mask .content img{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
  }
</style>